import { CANVAS, Main, PANEL, SIDE_MENU } from "../app";
import { ContextElement } from "./ContextElement";
import { ContextMenuAttributes } from "./ContextMenu";
import { GUIDraw } from "./GUIDraw";

export class SideMenu {
  private x: number;
  private y: number;
  private w: number;
  private h: number;

  private elements: ContextElement[];

  private static _instance: SideMenu;

  private constructor() {
    this.x = 0;
    this.y = 0;
    this.w = 0;
    this.h = 0;
    this.elements = [];
    this.setup();
  }

  public static get Instance() {
    return this._instance || (this._instance = new this());
  }

  public updateStyle() {
    SIDE_MENU.style.left = this.x.toString() + "px";
    SIDE_MENU.style.top = this.y.toString() + "px";
    SIDE_MENU.style.width = this.w.toString() + "px";
    //SIDE_MENU.style.height = this.h.toString() + "px";
  }

  public updateElements() {
    // for (let e of this.elements) {
    for (let i = 0; i < this.elements.length; i++) {
      SIDE_MENU.appendChild(this.elements[i].getHTMLElement());

      //Spacer
      //console.log(this.elements[i].getContent());
      if ((this.elements[i + 1] && this.elements[i + 1].getContent().startsWith("Generate")) || this.elements[i].getContent().startsWith("Generate")) {
        const el = document.createElement("div");
        el.className = "contextElement_spacer";
        SIDE_MENU.appendChild(el);
      }
    }
  }

  private setup() {
    let contents: ContextMenuAttributes[] = [
      { name: "Display Rays", id: "rays", type: "lever", value: Number(Main.Instance.getCTX().getDrawState().getDisplayRays()) },
      { name: "Display Shadow Rays", id: "shadowRays", type: "lever", value: Number(Main.Instance.getCTX().getDrawState().getDisplayShadowRays()) },
      { name: "Display Collision Data", id: "colData", type: "lever", value: Number(Main.Instance.getCTX().getDrawState().getDisplayColisionData()) },
      { name: "Shadow Ray Color", id: "shadowRayColor", type: "color", value: Main.Instance.getCTX().getDrawState().getShadowRayColor() },
      { name: "Display Animated Camera", id: "animCam", type: "lever", value: Number(Main.Instance.getCTX().getDrawState().getAnimatedCamera()) },
      { name: "Generate SVG", id: "genSVG", type: "", value: 0 },
      { name: "Dark Mode", id: "darkMode", type: "lever", value: Number(Main.Instance.getCTX().getDrawState().getDarkMode()) },
    ];

    this.x = Number(SIDE_MENU.style.left);
    this.y = Number(SIDE_MENU.style.top);

    for (let i = 0; i < contents.length; i++) {
      this.elements.push(new ContextElement(this.x, this.y + 17 * i, contents[i].name, contents[i].id, contents[i].type, contents[i].value, this));
    }
    this.updateElements();

    this.w = 300;
    this.h = 23 * this.elements.length;
    this.updateStyle();
  }

  public doFunction(func: string, val: number, e: any) {
    switch (func) {
      case "rays":
        Main.Instance.getCTX()
          .getDrawState()
          .setDisplayRays(val == 1);
        GUIDraw.Instance.needRedraw();
        break;
      case "shadowRays":
        Main.Instance.getCTX()
          .getDrawState()
          .setDisplayShadowRays(val == 1);
        GUIDraw.Instance.needRedraw();
        break;
      case "colData":
        Main.Instance.getCTX()
          .getDrawState()
          .setDisplayColisionData(val == 1);
        GUIDraw.Instance.needRedraw();
        break;
      case "darkMode":
        Main.Instance.getCTX()
          .getDrawState()
          .setDarkMode(val == 1);
        if (val == 1) {
          document.documentElement.style.setProperty("--text-color", "#c0c1c3");
          document.documentElement.style.setProperty("--background-color", "#36393f");
          document.documentElement.style.setProperty("--background-hover", "#727a86");
          document.body.style.backgroundColor = "black";
        } else {
          document.documentElement.style.setProperty("--text-color", "#151515");
          document.documentElement.style.setProperty("--background-color", "#b7bdc8");
          document.documentElement.style.setProperty("--background-hover", "#969fac");
          document.body.style.backgroundColor = "white";
        }
        GUIDraw.Instance.needRedraw();
        break;
      case "shadowRayColor":
        let colPickSh = document.getElementById("colorPicker_shadowRayColor") as HTMLInputElement;
        colPickSh.oninput = () => {
          Main.Instance.getCTX().getDrawState().setShadowRayColor(colPickSh.value);
        };
        GUIDraw.Instance.needRedraw();
        break;
      case "genSVG":
        //GENERATE SVG and prompt download
        let not_svg = document.getElementById("svg") as HTMLImageElement;
        if (not_svg != null) {
          let clonedSvgElement = CANVAS.cloneNode(true) as SVGElement;
          let style = document.createElement("style");
          style.innerHTML = `#canvas text {user-select: none;
          font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
          font-size: small;
          text-anchor: middle;}`;
          clonedSvgElement.appendChild(style);
          let outerHTML = clonedSvgElement.outerHTML,
            blob = new Blob([outerHTML], { type: "image/svg+xml;charset=utf-8" });
          let blobURL = URL.createObjectURL(blob);

          const link = document.createElement("a");
          link.style.display = "none";
          link.href = blobURL;
          link.download = "image.svg";

          document.body.appendChild(link);
          link.click();

          setTimeout(() => {
            URL.revokeObjectURL(link.href);
            link.parentNode?.removeChild(link);
          }, 0);

          //not_svg.src = blobURL;
        }
        break;
      case "animCam":
        Main.Instance.getCTX()
          .getDrawState()
          .setAnimatedCamera(val == 1);
        if (val == 1) {
          PANEL.style.display = "inherit";
        } else {
          PANEL.style.display = "none";
        }
        GUIDraw.Instance.needRedraw();
        break;
    }
  }
}
