import { Rect } from "../collision_classes/rect";
import { CANVAS, CONTEXT_MENU, Main, NAMING } from "../app";
import { ContextMenu, ContextMenuAttributes } from "./ContextMenu";
import { GUIDraw } from "./GUIDraw";
import { Vector } from "../collision_classes/vector";
import { Lamp } from "../collision_classes/lamp";
import { Material } from "../render_classes/material";
import { LampMaterial } from "../render_classes/lampMaterial";

export function addDraggable(svg: HTMLElement) {
  svg.addEventListener("mousedown", startDrag);
  svg.addEventListener("mousemove", drag);
  svg.addEventListener("mouseup", endDrag);
  svg.addEventListener("mouseleave", leaveDrag);
  svg.addEventListener("contextmenu", prevent);

  let spawn_id = 0;

  //console.log("SVG is draggable");

  let selectedElement: HTMLElement | null = null;

  function prevent(evt: MouseEvent) {
    evt.preventDefault();
    evt.stopPropagation();
  }

  function startDrag(evt: MouseEvent) {
    let el = evt.target as HTMLElement;
    // console.log(evt);
    ContextMenu.Instance.reset();
    if (el.getAttribute("draggable")) {
      selectedElement = el;
      if (selectedElement.ondrag != null) selectedElement.ondrag(evt as DragEvent);
    } else {
      //ContextMenu.Instance.reset();
      if (el.getAttribute("id") == "canvas") {
        canvasContextMenu(evt, el);
      }
    }
  }

  function drag(evt: MouseEvent) {
    //console.log(evt);
    if (selectedElement != null) {
      if (selectedElement.ondrag != null) selectedElement.ondrag(evt as DragEvent);
      GUIDraw.Instance.needRedraw();
    }
  }

  function endDrag(evt: MouseEvent) {
    selectedElement = null;
    //ContextMenu.Instance.reset();
  }

  function leaveDrag(evt: MouseEvent) {
    selectedElement = null;
    let rect = CONTEXT_MENU.getBoundingClientRect();
    if (evt.x < rect.left || evt.x > rect.right || evt.y < rect.top || evt.y > rect.bottom) ContextMenu.Instance.reset();
  }

  class CVS extends HTMLElement {
    public doFunction(func: string, val: number, e: MouseEvent) {}
  }

  function canvasContextMenu(evt: MouseEvent, el: HTMLElement) {
    (CANVAS as CVS).doFunction = doFunction;
    let pos = evt.offsetX + evt.offsetY * CANVAS.getBoundingClientRect().width;
    let attributes: ContextMenuAttributes[] = [
      { name: "New Rectangle", id: "rect", type: "", value: pos },
      { name: "New Lamp", id: "lamp", type: "", value: pos },
    ];
    if (evt.buttons == 2) ContextMenu.Instance.draw(evt.x, evt.y, attributes, el);
  }

  function doFunction(func: string, val: number, e: MouseEvent) {
    //console.log("doing", func);
    switch (func) {
      case "rect":
        Main.Instance.collisionCollection.push(
          new Rect(
            new Vector(val % CANVAS.getBoundingClientRect().width, val / CANVAS.getBoundingClientRect().width),
            50,
            50,
            0,
            new Material("#ffffff"),
            "spawned_rect" + spawn_id++,
            NAMING.pop()
          )
        );
        break;
      case "lamp":
        Main.Instance.lampCollection.push(
          new Lamp(
            new Vector(val % CANVAS.getBoundingClientRect().width, val / CANVAS.getBoundingClientRect().width),
            new LampMaterial("#ffffff"),
            "spawned_lamp" + spawn_id++
          )
        );
        break;
    }
  }
}
