import { Vector } from "./vector";
import { Line } from "./line";
import { DrawContext } from "../gui_classes/DrawContext";
import { CANVAS, CONTEXT_MENU } from "../app";
import { ContextMenuAttributes } from "../gui_classes/ContextMenu";
import { Material } from "../render_classes/material";

export class Rect {
  private pos: Vector;
  private width: number;
  private height: number;
  private rot: number;
  private material: Material;
  private vertecies: Vector[];
  private lines: Line[];
  private id: string;
  private name: string;
  private delete: boolean;
  private collisionIndex: number;

  constructor(position: Vector, width: number, height: number, rotation: number, material: Material, id: string, name: string = "") {
    this.pos = position;
    this.width = width;
    this.height = height;
    this.rot = rotation;
    this.material = material;
    this.id = id;
    this.name = name;
    this.delete = false;
    this.collisionIndex = 1;
    this.vertecies = [];
    this.lines = [];
    this.updateLines();
  }

  public updateLines() {
    this.lines = [];
    this.vertecies = [];

    this.vertecies.push(new Vector(-this.width / 2, -this.height / 2).getRotated(this.rot).getAddedVector(this.pos));
    this.vertecies.push(new Vector(this.width / 2, -this.height / 2).getRotated(this.rot).getAddedVector(this.pos));
    this.vertecies.push(new Vector(this.width / 2, this.height / 2).getRotated(this.rot).getAddedVector(this.pos));
    this.vertecies.push(new Vector(-this.width / 2, this.height / 2).getRotated(this.rot).getAddedVector(this.pos));

    this.lines.push(new Line(this.vertecies[0], new Vector(this.width, 0).getRotated(this.rot), 1));
    this.lines.push(new Line(this.vertecies[1], new Vector(0, this.height).getRotated(this.rot), 1));
    this.lines.push(new Line(this.vertecies[2], new Vector(-this.width, 0).getRotated(this.rot), 1));
    this.lines.push(new Line(this.vertecies[3], new Vector(0, -this.height).getRotated(this.rot), 1));
  }

  public getDelete() {
    return this.delete;
  }

  public getLines() {
    return this.lines;
  }

  public getPosition() {
    return this.pos;
  }

  public getWidth() {
    return this.width;
  }

  public getHeight() {
    return this.height;
  }

  public getMaterial() {
    return this.material;
  }

  public getColor() {
    return this.material.getColor();
  }

  public getRotation() {
    return this.rot;
  }

  public setRotation(rotation: number) {
    this.rot = rotation;
  }

  public getId() {
    return this.id;
  }

  public getName() {
    return this.name;
  }

  getCollisionIndex() {
    return this.collisionIndex;
  }

  getAndIncrementCollisionIndex() {
    return this.collisionIndex++;
  }

  public draw(ctx: DrawContext) {
    let i = 0;
    for (let l of this.lines) {
      //new Line(l.getPosition(), l.getNormal(), 1).draw(ctx, this.id + "_" + (i++).toString()); // Normals
      //l.draw(ctx, this.id + "_" + (i++).toString());
    }
    ctx.pushStyle();
    ctx.setFill(this.getColor());
    ctx.addRect(
      this.vertecies[0].getX(),
      this.vertecies[0].getY(),
      this.vertecies[1].getX(),
      this.vertecies[1].getY(),
      this.vertecies[2].getX(),
      this.vertecies[2].getY(),
      this.vertecies[3].getX(),
      this.vertecies[3].getY(),
      this.id,
      this
    );
    ctx.popStyle();
    this.collisionIndex = 1;
  }

  move(x: number, y: number, dx: number, dy: number) {
    //this.pos.add(dx, dy);
    this.pos = new Vector(x - CANVAS.getBoundingClientRect().left, y - CANVAS.getBoundingClientRect().top);
    this.updateLines();
  }

  rotate(x: number, y: number, dx: number, dy: number) {
    this.rot = -Math.atan2(x - CANVAS.getBoundingClientRect().left - this.pos.getX(), y - CANVAS.getBoundingClientRect().top - this.pos.getY());
    this.updateLines();
  }

  doFunction(func: string, val: number, e: any) {
    if (func == "rotate") {
      this.rotate(e.x, e.y, e.movementX, e.movementY);
    } else if (func == "rotation") {
      let numberPick = document.getElementById("numberPicker_rotation") as HTMLInputElement;
      if (numberPick == null) {
      } else {
        numberPick.oninput = () => {
          this.rot = (Number(numberPick.value) / 180) * Math.PI;
          this.updateLines();
        };
      }
    } else if (func == "color") {
      let colPick = document.getElementById("colorPicker_color") as HTMLInputElement;
      if (colPick == null) {
        colPick = document.createElement("input");
        colPick.type = "color";
        colPick.className = "contextElement_input";
        colPick.id = "colorPicker_color";
        colPick.value = this.getColor();
        colPick.select();
        colPick.oninput = () => {
          this.material.setColor(colPick.value);
        };
        CONTEXT_MENU.firstChild?.appendChild(colPick);
      } else {
        colPick.oninput = () => {
          this.material.setColor(colPick.value);
        };
      }
      // if (colPick != null) {
      //   this.color = colPick.value;
      // }
    } else if (func == "diffuse") {
      let numberPick = document.getElementById("numberPicker_diffuse") as HTMLInputElement;
      if (numberPick == null) {
      } else {
        numberPick.oninput = () => {
          if (Number(numberPick.value) < 0) {
            numberPick.value = "0";
          } else if (Number(numberPick.value) > 100) {
            numberPick.value = "100";
          }
          this.material.setDiffuse(Number(numberPick.value) / 100);
          this.updateLines();
        };
      }
    } else if (func == "specular") {
      let numberPick = document.getElementById("numberPicker_specular") as HTMLInputElement;
      if (numberPick == null) {
      } else {
        numberPick.oninput = () => {
          if (Number(numberPick.value) < 0) {
            numberPick.value = "0";
          } else if (Number(numberPick.value) > 100) {
            numberPick.value = "100";
          }
          this.material.setSpecular(Number(numberPick.value) / 100);
          this.updateLines();
        };
      }
    } else if (func == "width") {
      let numberPick = document.getElementById("numberPicker_width") as HTMLInputElement;
      if (numberPick == null) {
      } else {
        numberPick.oninput = () => {
          this.width = Number(numberPick.value);
          this.updateLines();
        };
      }
    } else if (func == "height") {
      let numberPick = document.getElementById("numberPicker_height") as HTMLInputElement;
      if (numberPick == null) {
      } else {
        numberPick.oninput = () => {
          this.height = Number(numberPick.value);
          this.updateLines();
        };
      }
    } else if (func == "delete") {
      this.delete = true;
    }
  }

  getContextAttributes(): ContextMenuAttributes[] {
    return [
      { name: "Color", id: "color", type: "color", value: this.getColor() },
      { name: "Diffuse (%)", id: "diffuse", type: "number", value: this.material.getDiffuse() * 100 },
      { name: "Specular (%)", id: "specular", type: "number", value: this.material.getSpecular() * 100 },
      { name: "Rotation", id: "rotation", type: "number", value: Math.round((this.rot / Math.PI) * 180) },
      { name: "Width", id: "width", type: "number", value: this.width },
      { name: "Height", id: "height", type: "number", value: this.height },
      { name: "Remove", id: "delete", type: "", value: 0 },
    ];
  }

  /**
   * @description Calculates the collision point between this rect and line
   * @param  {Line} line Line to check collision with
   * @returns {[number | undefined, number | undefined, number, number, Line, Line]} Array with collision data
   */
  getIntersect(line: Line): [number, number, number, number, Line, Line][] {
    let result: [number, number, number, number, Line, Line][] = [];
    for (let l of this.lines) {
      let [x, y, t1, t2, line1, line2] = l.getIntersect(line);
      if (x != undefined && y != undefined && t1 != undefined && t2 != undefined && t1 > 0 && t1 < l.getLength() && t2 > 0 && t2 < line.getLength()) {
        result.push([x, y, t1, t2, line1, line2]);
      }
    }
    return result;
  }
}
