import { height, width } from "../app";
import { DrawContext } from "../gui_classes/DrawContext";
import { Line } from "./line";
import { Rect } from "./rect";
import { Vector } from "./vector";
import { CollisionEvent, CollisionLineEvent, CollisionRectEvent } from "./collisionEvent";
import { Lamp } from "./lamp";
import { Ray } from "./ray";

export class ShadowRay {
  private line: Line;
  private blocked: boolean;
  private lamp: Lamp;
  private id: string;

  constructor(position: Vector, direction: Vector, lamp: Lamp, id: string) {
    this.line = new Line(position, direction, 1);
    this.lamp = lamp;
    this.blocked = false;
    this.id = id;
  }

  public getBlocked() {
    return this.blocked;
  }

  public getLamp() {
    return this.lamp;
  }

  public getLine() {
    return this.line;
  }

  public draw(ctx: DrawContext, onlyCollision = false) {
    if (!onlyCollision) this.line.draw(ctx, this.id, true);
  }

  private reset() {
    this.line = new Line(this.line.getPosition(), this.line.getDirection(), 1);
  }

  public recalculateRay(collisionCollection: (Line | Rect)[], lamps: Lamp[]) {
    this.reset();
    let collisionLines: Line[] | undefined;
    let colEvent: CollisionLineEvent | CollisionRectEvent;
    for (let el of collisionCollection) {
      if (el instanceof Line) {
        colEvent = new CollisionLineEvent(this.line, el);
        if (colEvent.getNewLines().length > 0) {
          this.line = colEvent.getLine();
          collisionLines = colEvent.getNewLines();
        }
      } else if (el instanceof Rect) {
        let intersects = el.getIntersect(this.line);
        if (intersects != null && intersects.length > 0) {
          for (let [x, y, t1, t2, line1, line2] of intersects) {
            if (t2 != undefined && t2 > 0 && t2 < this.line.getLength()) {
              let tempLine = new Line(this.line.getPosition(), this.line.getDirection(), t2);
              if (tempLine.getRealLength() > 0) {
                this.line = tempLine;
                collisionLines = [
                  new Line(new Vector(x, y), line1.getNormal().getRotated(line1.getNormal().getAngleBetween(this.line.getDirection().getInverted())), 1),
                ];
                this.blocked = true;
              }
            }
          }
        }
      }
    }
  }
}
