import { CANVAS } from "../app";
import { DrawContext } from "./DrawContext";

export class GUIDraw {
  private static _instance: GUIDraw;
  private redraw = false;

  private constructor() {
    CANVAS.setAttribute("xmlns", "http://www.w3.org/2000/svg");
    CANVAS.setAttribute("version", "1.1");
    CANVAS.setAttribute("baseProfile", "full");
  }

  public static get Instance() {
    return this._instance || (this._instance = new this());
  }

  needRedraw() {
    this.redraw = true;
  }

  draw(ctx: DrawContext) {
    CANVAS.setAttribute("width", ctx.getWidth().toString());
    CANVAS.setAttribute("height", ctx.getHeight().toString());
    if (CANVAS.innerHTML == "" || this.redraw) {
      //span.style.width = "400px";
      //span.style.height = "400px";
      //span.setAttribute("viewbox", "0 0 400 400");
      //console.log("Redraw");
      CANVAS.style.backgroundColor = ctx.getDrawState().getDarkMode() ? "#000" : "#fff";
      CANVAS.innerHTML = ctx.getSVG(); //['<img src="', "data:image/svg+xml;charset=utf-8," + encodeURIComponent(ctx.getSVG()), '" title="', "out.svg", '"/>'].join("");

      //GENERATE SVG as OUTPUT
      let not_svg = document.getElementById("svg") as HTMLImageElement;
      if (false && not_svg != null) {
        let clonedSvgElement = CANVAS.cloneNode(true) as SVGElement;
        let outerHTML = clonedSvgElement.outerHTML,
          blob = new Blob([outerHTML], { type: "image/svg+xml;charset=utf-8" });
        let blobURL = URL.createObjectURL(blob);
        not_svg.src = blobURL;
      }

      //output.insertBefore(span, null);
    } else {
      //canvas.innerHTML = ctx.getSVG(); //['<img src="', "data:image/svg+xml;charset=utf-8," + encodeURIComponent(ctx.getSVG()), '" title="', "out.svg", '"/>'].join("");
    }
    this.redraw = false;
  }
}
