import { DrawContext } from "./DrawContext";
import { GUIDraw } from "./GUIDraw";

export class MainHelper {
  protected ctx!: DrawContext;

  doDraw() {
    this.draw();
    GUIDraw.Instance.draw(this.ctx);
    this.ctx.clearSVG();
  }

  public getCTX() {
    return this.ctx;
  }

  draw() {}
}
