import { DrawContext } from "./DrawContext";

export class SVGStyle {
  private stroke: string;
  private fill: string;
  private strokeWidth: string;

  public constructor(ctx: DrawContext, stroke?: string, fill?: string, strokeWidth?: string) {
    this.stroke = ctx.getDrawState().getDarkMode() ? "white" : "black";
    this.fill = "none";
    this.strokeWidth = "1.5px";
  }

  public setStroke(s: string) {
    this.stroke = s;
  }

  public setFill(s: string) {
    this.fill = s;
  }

  public setStrokeWidth(s: string) {
    this.strokeWidth = s;
  }

  public getStroke() {
    return this.stroke;
  }

  public getFill() {
    return this.fill;
  }

  public getStrokeWidth() {
    return this.strokeWidth;
  }
}
