import { PANEL } from "../app";

export function SetupPanel() {
  (PANEL.children.namedItem("render_slider_play") as HTMLButtonElement).onclick = function () {
    if (PANEL.getAttribute("play") == "true") {
      PANEL.setAttribute("play", "false");
      (this as HTMLDivElement).innerHTML = `<div class="mdc-icon-button__ripple"></div>play_arrow`;
    } else {
      PANEL.setAttribute("play", "true");
      (this as HTMLDivElement).innerHTML = `<div class="mdc-icon-button__ripple"></div>pause`;
    }
  };
  const PANEL_DRAG = PANEL.children.namedItem("panel_drag") as HTMLButtonElement;
  PANEL_DRAG.onmousedown = function (e) {
    (this as HTMLDivElement).setAttribute("dragging", "true");
  };
  PANEL_DRAG.onmouseup = function (e) {
    (this as HTMLDivElement).setAttribute("dragging", "false");
  };
  PANEL_DRAG.onmousemove = function (e) {
    //console.log(e);
    if (PANEL_DRAG.getAttribute("dragging") == "true") {
      PANEL.style.left =
        "max(0px,min(calc(100vw - " + PANEL.clientWidth + "px)," + (e.pageX - PANEL_DRAG.offsetLeft - PANEL_DRAG.clientWidth / 2).toString() + "px))";
      PANEL.style.top =
        "max(0px,min(calc(150vh - " + PANEL.clientHeight + "px)," + (e.pageY - PANEL_DRAG.offsetTop - PANEL_DRAG.clientHeight / 2).toString() + "px))";
    }
  };
  PANEL_DRAG.onmouseleave = PANEL_DRAG.onmousemove;
  PANEL_DRAG.onmouseout = PANEL_DRAG.onmousemove;
}
