import { Vector } from "../collision_classes/vector";
import { DrawContext } from "../gui_classes/DrawContext";

export class ProjectLine {
  private pos: Vector;
  private dir: Vector;
  private size: number;
  private id: string;
  private colors: string[];
  constructor(pos: Vector, dir: Vector, size: number, id: string) {
    this.pos = pos;
    this.dir = dir;
    this.size = size;
    this.id = id;
    this.colors = ["#000000"];
  }

  public getSize() {
    return this.size;
  }

  public setSize(size: number) {
    this.size = size;
  }

  public setColors(colors: string[]) {
    this.colors = colors.length < 1 ? ["#000000"] : colors;
  }

  public setPosition(pos: Vector) {
    this.pos = pos;
  }

  public setDirection(dir: Vector) {
    this.dir = dir;
  }

  public draw(ctx: DrawContext) {
    let delta = this.size / this.colors.length;
    let width = 5;
    let normal = this.dir.getNormal().getInverted();
    ctx.pushStyle();
    ctx.setFill("#808080");
    ctx.addRect(
      this.pos.getAddedVector(this.dir.getMult(0)).getX(),
      this.pos.getAddedVector(this.dir.getMult(0)).getY(),
      this.pos.getAddedVector(this.dir.getMult(width)).getX(),
      this.pos.getAddedVector(this.dir.getMult(width)).getY(),
      this.pos.getAddedVector(this.dir.getMult(width).getAddedVector(normal.getMult(this.size))).getX(),
      this.pos.getAddedVector(this.dir.getMult(width).getAddedVector(normal.getMult(this.size))).getY(),
      this.pos.getAddedVector(this.dir.getMult(0).getAddedVector(normal.getMult(this.size))).getX(),
      this.pos.getAddedVector(this.dir.getMult(0).getAddedVector(normal.getMult(this.size))).getY(),
      this.id + "_bg"
    );
    for (let i = 0; i < this.colors.length; i++) {
      ctx.setFill(this.colors[i]);
      ctx.setStroke("none");
      ctx.addRect(
        this.pos.getAddedVector(this.dir.getMult(0).getAddedVector(normal.getMult(delta * i))).getX(),
        this.pos.getAddedVector(this.dir.getMult(0).getAddedVector(normal.getMult(delta * i))).getY(),
        this.pos.getAddedVector(this.dir.getMult(width).getAddedVector(normal.getMult(delta * i))).getX(),
        this.pos.getAddedVector(this.dir.getMult(width).getAddedVector(normal.getMult(delta * i))).getY(),
        this.pos.getAddedVector(this.dir.getMult(width).getAddedVector(normal.getMult(delta * i + delta))).getX(),
        this.pos.getAddedVector(this.dir.getMult(width).getAddedVector(normal.getMult(delta * i + delta))).getY(),
        this.pos.getAddedVector(this.dir.getMult(0).getAddedVector(normal.getMult(delta * i + delta))).getX(),
        this.pos.getAddedVector(this.dir.getMult(0).getAddedVector(normal.getMult(delta * i + delta))).getY(),
        this.id + "_" + i
      );
    }
    ctx.popStyle();
  }
}
