export class DrawState {
  displayRays: boolean;
  displayShadowRays: boolean;
  displayColisionData: boolean;
  shadowRayColor: string;
  animatedCamera: boolean;
  darkMode: boolean;

  constructor() {
    this.displayRays = true;
    this.displayShadowRays = true;
    this.displayColisionData = true;
    this.shadowRayColor = "#909090";

    this.animatedCamera = false;

    this.darkMode = false;
  }

  setDisplayRays(b: boolean) {
    this.displayRays = b;
  }

  getDisplayRays(): boolean {
    return this.displayRays;
  }

  setDisplayShadowRays(b: boolean) {
    this.displayShadowRays = b;
  }

  getDisplayShadowRays(): boolean {
    return this.displayShadowRays;
  }

  setDisplayColisionData(b: boolean) {
    this.displayColisionData = b;
  }

  getDisplayColisionData(): boolean {
    return this.displayColisionData;
  }

  setAnimatedCamera(b: boolean) {
    this.animatedCamera = b;
  }

  getAnimatedCamera(): boolean {
    return this.animatedCamera;
  }

  setDarkMode(b: boolean) {
    this.darkMode = b;
  }

  getDarkMode(): boolean {
    return this.darkMode;
  }

  setShadowRayColor(b: string) {
    this.shadowRayColor = b;
  }

  getShadowRayColor(): string {
    return this.shadowRayColor;
  }
}
