export class Material {
  private color: string;
  private diffuse: number;
  private specular: number;
  constructor(color: string, diffuse: number = 1, specular: number = 0.5) {
    this.color = color;
    this.diffuse = diffuse;
    this.specular = specular;
  }

  public getColor() {
    return this.color;
  }

  public getDiffuse() {
    return this.diffuse;
  }

  public getSpecular() {
    return this.specular;
  }

  public setColor(color: string) {
    this.color = color;
  }

  public setDiffuse(diffuse: number) {
    this.diffuse = this.constrain(diffuse);
  }

  public setSpecular(specular: number) {
    this.specular = this.constrain(specular);
  }

  //Constrain number between 0 and 1
  private constrain(x: number) {
    return x < 0 ? 0 : x > 1 ? 1 : x;
  }
}
