import { Rect } from "../collision_classes/rect";
import { CANVAS } from "../app";
import { ContextMenu } from "./ContextMenu";
import { GUIDraw } from "./GUIDraw";
import { SVGStyle } from "./SVGStyle";
import { Lamp } from "../collision_classes/lamp";
import { Camera } from "../collision_classes/camera";
import { DrawState } from "./DrawState";
import { Vector } from "../collision_classes/vector";
import { Collision } from "../collision_classes/collision";

export class DrawContext {
  private svg: string;
  private width: number;
  private height: number;
  private style: SVGStyle[];
  private drawState: DrawState;

  public constructor(width: number, height: number) {
    this.svg = "";
    this.width = width;
    this.height = height;
    this.style = [];
    this.drawState = new DrawState();
    this.style.push(new SVGStyle(this));
  }

  public getSVG() {
    return this.svg; //`<svg xmlns="http://www.w3.org/2000/svg" version="1.1" baseProfile="full" width="${this.width}px" height="${this.height}px">` + this.svg + `</svg>`;
  }

  public clearSVG() {
    this.svg = "";
  }

  public getWidth() {
    return this.width;
  }

  public getHeight() {
    return this.height;
  }

  public pushStyle() {
    this.style.push(new SVGStyle(this));
  }

  public popStyle() {
    this.style.pop();
  }

  private getStyle() {
    return this.style[this.style.length - 1];
  }

  public getDrawState() {
    return this.drawState;
  }

  public setStroke(s: string) {
    this.getStyle().setStroke(s);
  }

  public setFill(s: string) {
    this.getStyle().setFill(s);
  }

  public addLine(x1: number, y1: number, x2: number, y2: number, id: string, dashed = false) {
    this.svg += `\n<line id="${id}" x1="${x1}" y1="${y1}" x2="${x2}" y2="${y2}" stroke="${this.getStyle().getStroke()}" 
    stroke-dasharray="${dashed ? "8" : ""}"></line>`;
    if (document.getElementById(id) == null) {
      // let el = document.createElement("line") as unknown as SVGLineElement;
      // el.id = id;
      // el.setAttribute("x1", `${x1}`);
      // el.setAttribute("y1", `${y1}`);
      // el.setAttribute("x2", `${x2}`);
      // el.setAttribute("y2", `${y2}`);
      // el.setAttribute("stroke", `${this.getStyle().getStroke()}`);
      // el.x1.baseVal.value = x1;
      // el.y1.baseVal.value = y1;
      // el.x2.baseVal.value = x2;
      // el.y2.baseVal.value = y2;
      // el.style.stroke = "black";
      // canvas.insertBefore(el, null);
      //GUIDraw.Instance.addElement(el); //`\n<line id="${id}" x1="${x1}" y1="${y1}" x2="${x2}" y2="${y2}" stroke="${this.getStyle().getStroke()}" fill="${this.getStyle().getFill()}" ></line>`
      GUIDraw.Instance.needRedraw();
    } else {
      let el = document.getElementById(id) as unknown as SVGLineElement;
      el.setAttribute("x1", `${x1}`);
      el.setAttribute("y1", `${y1}`);
      el.setAttribute("x2", `${x2}`);
      el.setAttribute("y2", `${y2}`);
      el.setAttribute("stroke", `${this.getStyle().getStroke()}`);
      // el.setAttribute("stroke-width", `${this.get()}`);
      // el.onclick = (e) => {
      //   console.log(e.target);
      // };
    }
  }

  public addCam(x1: number, y1: number, direction: Vector, size: number, id: string, name: string, cam: Camera) {
    let normal = direction.getNormal();
    let pos = new Vector(x1, y1);
    let v1 = pos.getAddedVector(direction.getMult(size / 3).getAddedVector(normal.getMult(size / 3)));
    let v1_2 = pos.getAddedVector(direction.getMult(size / 3).getAddedVector(normal.getMult(-size / 3)));
    let v2 = pos.getAddedVector(direction.getMult(size).getAddedVector(normal.getMult(size)));
    let v3 = pos.getAddedVector(direction.getMult(size).getAddedVector(normal.getMult(-size)));
    let v4 = pos.getAddedVector(direction.getMult(size / 3).getAddedVector(normal.getMult(-size * 1.2)));
    let v5 = pos.getAddedVector(direction.getMult(-size).getAddedVector(normal.getMult(-size * 1.2)));
    let v6 = pos.getAddedVector(direction.getMult(-size).getAddedVector(normal.getMult(size * 1.2)));
    let v7 = pos.getAddedVector(direction.getMult(size / 3).getAddedVector(normal.getMult(size * 1.2)));

    let vt = new Vector((v4.getX() + v5.getX() + v6.getX() + v7.getX()) / 4, (v4.getY() + v5.getY() + v6.getY() + v7.getY()) / 4);
    vt.addVector(pos.getAddedVector(vt.getInverted()).getMult(0.5));

    this.svg += `\n<polygon id="${id}" points="${v1.getX()},${v1.getY()} ${v2.getX()},${v2.getY()} ${v3.getX()},${v3.getY()} ${v1_2.getX()},${v1_2.getY()}
    ${v4.getX()},${v4.getY()} ${v5.getX()},${v5.getY()} ${v6.getX()},${v6.getY()} ${v7.getX()},${v7.getY()}" 
    stroke="${this.getStyle().getStroke()}" fill="${this.getStyle().getFill()}" draggable="true"></polygon>
    
    <text x="${vt.getX()}" y="${vt.getY()}" 
    transform="rotate(${-(new Vector(0, 1).getAngleBetween(direction) / Math.PI) * 180},${vt.getX()},${vt.getY()})"
    fill="${this.getDrawState().getDarkMode() ? "white" : "black"}">Camera ${name}</text>`;
    if (document.getElementById(id) == null) {
      GUIDraw.Instance.needRedraw();
    } else {
      let el = document.getElementById(id) as unknown as SVGPolygonElement;
      el.setAttribute(
        "points",
        `${v1.getX()},${v1.getY()} ${v2.getX()},${v2.getY()} ${v3.getX()},${v3.getY()} ${v1_2.getX()},${v1_2.getY()}
      ${v4.getX()},${v4.getY()} ${v5.getX()},${v5.getY()} ${v6.getX()},${v6.getY()} ${v7.getX()},${v7.getY()}`
      );
      el.setAttribute("stroke", `${this.getStyle().getStroke()}`);
      el.setAttribute("fill", `${this.getStyle().getFill()}`);
      // el.onclick = (e) => {
      //   console.log(e.target);
      // };
      el.setAttribute("draggable", "true");
      el.ondrag = (e) => {
        e.preventDefault();
        if (e.buttons == 1 && e.shiftKey == false) cam.move(e.x, e.y, e.movementX, e.movementY);
        if (e.buttons == 1 && e.shiftKey) cam.rotate(e.x, e.y, e.movementX, e.movementY);
        if (e.buttons == 2) ContextMenu.Instance.draw(e.x, e.y, cam.getContextAttributes(), cam); //rect.rotate(e.x, e.y, e.movementX, e.movementY);
      };
    }
  }

  public addRect(x1: number, y1: number, x2: number, y2: number, x3: number, y3: number, x4: number, y4: number, id: string, rect?: Rect) {
    let vt = new Vector((x1 + x2 + x3 + x4) / 4, (y1 + y2 + y3 + y4) / 4 + 4);

    this.svg += `\n<polygon id="${id}" points="${x1},${y1} ${x2},${y2} ${x3},${y3} ${x4},${y4}" stroke="${this.getStyle().getStroke()}" fill="${this.getStyle().getFill()}" draggable="${
      rect != undefined
    }"></polygon>`;
    if (rect != undefined) {
      this.svg += `<text x="${vt.getX()}" y="${vt.getY()}">${rect?.getName()}</text>`;
      //transform="rotate(${((rect?.getRotation()) / Math.PI) * 180 + 90},${vt.getX()},${vt.getY()})"
    }
    if (document.getElementById(id) == null) {
      GUIDraw.Instance.needRedraw();
    } else {
      let el = document.getElementById(id) as unknown as SVGPolygonElement;
      el.setAttribute("points", `${x1},${y1} ${x2},${y2} ${x3},${y3} ${x4},${y4}`);
      el.setAttribute("stroke", `${this.getStyle().getStroke()}`);
      el.setAttribute("fill", `${this.getStyle().getFill()}`);
      // el.onclick = (e) => {
      //   console.log(e.target);
      // };
      if (rect != undefined) {
        el.setAttribute("draggable", "true");
        el.ondrag = (e) => {
          e.preventDefault();
          if (e.buttons == 1 && e.shiftKey == false) rect.move(e.x, e.y, e.movementX, e.movementY);
          if (e.buttons == 1 && e.shiftKey) rect.rotate(e.x, e.y, e.movementX, e.movementY);
          if (e.buttons == 2) ContextMenu.Instance.draw(e.x, e.y, rect.getContextAttributes(), rect); //rect.rotate(e.x, e.y, e.movementX, e.movementY);
        };
      }
    }
  }

  public addCircle(x1: number, y1: number, r: number, id: string, lamp: Lamp) {
    this.svg += `\n<circle id="${id}" cx="${x1}" cy="${y1}" r="${r}" stroke="${this.getStyle().getStroke()}" fill="${this.getStyle().getFill()}" draggable="true"></circle>`;
    if (document.getElementById(id) == null) {
      GUIDraw.Instance.needRedraw();
    } else {
      let el = document.getElementById(id) as unknown as SVGPolygonElement;
      el.setAttribute("cx", `${x1}`);
      el.setAttribute("cy", `${y1}`);
      el.setAttribute("r", `${r}`);
      el.setAttribute("stroke", `${this.getStyle().getStroke()}`);
      el.setAttribute("fill", `${this.getStyle().getFill()}`);
      // el.onclick = (e) => {
      //   console.log(e.target);
      // };
      el.setAttribute("draggable", "true");
      el.ondrag = (e) => {
        e.preventDefault();
        if (e.buttons == 1 && e.shiftKey == false) lamp.move(e.x, e.y, e.movementX, e.movementY);
        if (e.buttons == 2) ContextMenu.Instance.draw(e.x, e.y, lamp.getContextAttributes(), lamp); //rect.rotate(e.x, e.y, e.movementX, e.movementY);
      };
    }
  }

  public addCollision(x1: number, y1: number, r: number, id: string, collision: Collision) {
    this.svg += `\n<circle id="${id}" cx="${x1}" cy="${y1}" r="${r}" stroke="${this.getStyle().getStroke()}" fill="${this.getStyle().getFill()}" draggable="false"></circle>`;
    if (document.getElementById(id) == null) {
      GUIDraw.Instance.needRedraw();
    } else {
      let el = document.getElementById(id) as unknown as SVGPolygonElement;
      el.setAttribute("cx", `${x1}`);
      el.setAttribute("cy", `${y1}`);
      el.setAttribute("r", `${r}`);
      el.setAttribute("stroke", `${this.getStyle().getStroke()}`);
      el.setAttribute("fill", `${this.getStyle().getFill()}`);
      // el.onclick = (e) => {
      //   console.log(e.target);
      // };
      el.setAttribute("draggable", "false");
      el.ondrag = (e) => {
        e.preventDefault();
        //if (e.buttons == 2) ContextMenu.Instance.draw(e.x, e.y, collision.getContextAttributes(), collision); //rect.rotate(e.x, e.y, e.movementX, e.movementY);
      };
    }
  }

  public addText(x1: number, y1: number, text: string, id: string) {
    this.svg += `<text id="${id}" x="${x1}" y="${y1}" fill="${this.getStyle().getFill()}" draggable="false">${text}</text>`;
    if (document.getElementById(id) == null) {
      GUIDraw.Instance.needRedraw();
    } else {
      let el = document.getElementById(id) as unknown as SVGPolygonElement;
      el.setAttribute("x", `${x1}`);
      el.setAttribute("y", `${y1}`);
      el.innerHTML = text;
      el.setAttribute("style", `fill: ${this.getStyle().getFill()};`);
      // el.setAttribute("stroke", `${this.getStyle().getStroke()}`);
      // el.setAttribute("fill", `${this.getStyle().getFill()}`);
      el.setAttribute("draggable", "false");
    }
  }

  public addArrow(x1: number, y1: number, x2: number, y2: number, id: string) {
    let dir = new Vector(x2 - x1, y2 - y1).getNormalized().getMult(-5);
    let nor = dir.getNormal().getNormalized().getMult(5);
    let v1 = new Vector(x2, y2).getAddedVector(dir).getAddedVector(nor);
    let v2 = new Vector(x2, y2).getAddedVector(dir).getAddedVector(nor.getInverted());
    this.svg += `<path id="${id}" d="M ${x1},${y1} L ${x2},${y2} ${v1.getX()},${v1.getY()} ${x2},${y2} ${v2.getX()},${v2.getY()}" 
                  stroke="${this.getStyle().getStroke()}" fill="none" draggable="false"></path>`;
    if (document.getElementById(id) == null) {
      GUIDraw.Instance.needRedraw();
    } else {
      let el = document.getElementById(id) as unknown as SVGPolygonElement;
      el.setAttribute("d", `M ${x1},${y1} L ${x2},${y2} ${v1.getX()},${v1.getY()} ${x2},${y2} ${v2.getX()},${v2.getY()}`);
      el.setAttribute("stroke", `${this.getStyle().getStroke()}`);
      el.setAttribute("fill", `none`);
      el.setAttribute("draggable", "false");
    }
  }

  public addArc(x1: number, y1: number, rx: number, ry: number, phi: number, lf: number, sf: number, x2: number, y2: number, id: string) {
    this.svg += `<path id="${id}" d="M ${x1},${y1} A ${rx},${ry},${phi},${lf},${sf},${x2},${y2}" 
                  stroke="${this.getStyle().getStroke()}" fill="none" draggable="false"></path>`;
    if (document.getElementById(id) == null) {
      GUIDraw.Instance.needRedraw();
    } else {
      let el = document.getElementById(id) as unknown as SVGPolygonElement;
      el.setAttribute("d", `M ${x1},${y1} A ${rx},${ry},${phi},${lf},${sf},${x2},${y2}`);
      el.setAttribute("stroke", `${this.getStyle().getStroke()}`);
      el.setAttribute("fill", `none`);
      el.setAttribute("draggable", "false");
    }
  }

  addArc2(arcEnd2: Vector, arcMid2: Vector, arcMid1: Vector, arcEnd1: Vector, id: string) {
    this.svg += `<path id="${id}" d="M ${arcEnd2.getX()},${arcEnd2.getY()} C ${arcMid2.getX()},${arcMid2.getY()} ${arcMid1.getX()},${arcMid1.getY()} ${arcEnd1.getX()},${arcEnd1.getY()}" 
                  stroke="${this.getStyle().getStroke()}" fill="none" draggable="false"></path>`;
    if (document.getElementById(id) == null) {
      GUIDraw.Instance.needRedraw();
    } else {
      let el = document.getElementById(id) as unknown as SVGPolygonElement;
      el.setAttribute(
        "d",
        `M ${arcEnd2.getX()},${arcEnd2.getY()} C ${arcMid2.getX()},${arcMid2.getY()} ${arcMid1.getX()},${arcMid1.getY()} ${arcEnd1.getX()},${arcEnd1.getY()}`
      );
      el.setAttribute("stroke", `${this.getStyle().getStroke()}`);
      el.setAttribute("fill", `none`);
      el.setAttribute("draggable", "false");
    }
  }
}
