import { height, Main, width } from "../app";
import { DrawContext } from "../gui_classes/DrawContext";
import { Line } from "./line";
import { Rect } from "./rect";
import { Vector } from "./vector";
import { CollisionEvent, CollisionLineEvent, CollisionRectEvent } from "./collisionEvent";
import { Lamp } from "./lamp";
import { ShadowRay } from "./shadowRay";
import { Ray } from "./ray";

export class Collision {
  private pos: Vector;
  private id: string;

  private prevRay: Ray;
  private depth: number;
  private maxDepth: number;
  private nextRay: null | Ray;
  private shadowRays: ShadowRay[];
  private collisionObject: Rect;
  private collisionLine: Line;
  private collisionAxis: Vector;
  private reflectingIntensities: [number, number, number];

  constructor(position: Vector, collisionObject: Rect, collisionLine: Line, collisionAxis: Vector, depth: number, maxDepth: number, prevRay: Ray, id: string) {
    this.pos = position;
    this.id = id;
    this.prevRay = prevRay;
    this.maxDepth = maxDepth;
    this.depth = depth;
    this.nextRay = null;
    this.shadowRays = [];
    this.collisionObject = collisionObject;
    this.collisionLine = collisionLine;
    this.collisionAxis = collisionAxis;
    this.reflectingIntensities = [0, 0, 0];
  }

  public getNextRay() {
    return this.nextRay;
  }

  public getShadowRays() {
    return this.shadowRays;
  }

  public getId() {
    return this.id;
  }

  public getDepth() {
    return this.depth;
  }

  public getMaxDepth() {
    return this.maxDepth;
  }

  public getReflectingIntensities() {
    return this.reflectingIntensities;
  }

  public draw(ctx: DrawContext, onlyCollision = false) {
    if (onlyCollision) {
      if (ctx.getDrawState().getDisplayColisionData()) {
        ctx.pushStyle();
        ctx.setFill(ctx.getDrawState().getDarkMode() ? "white" : "black");
        let arrEnd = this.pos.getAddedVector(this.collisionAxis.getNormalized().getMult(50));
        let arcStart = this.pos.getAddedVector(this.collisionAxis.getNormalized().getMult(40));
        if (this.nextRay != undefined) {
          let arcEnd1 = this.pos.getAddedVector(this.nextRay.getLine().getDirection().getNormalized().getMult(40));
          let arcEnd2 = this.pos.getAddedVector(this.prevRay.getLine().getDirection().getNormalized().getMult(-40));
          let arcMid = arcEnd1.getAddedVector(arcEnd2.getInverted());
          let arcMid1 = arcStart.getAddedVector(arcMid.getMult(0.25));
          let arcMid2 = arcStart.getAddedVector(arcMid.getMult(-0.25));

          let testAngle = this.collisionLine.getNormal().getAngleBetween(this.prevRay.getLine().getDirection().getInverted()) - Math.PI / 2;
          testAngle = testAngle > Math.PI ? -Math.PI + (testAngle - Math.PI) : testAngle;
          //console.log(this.id, testAngle);
          let sf = testAngle > Math.PI || testAngle < 0 ? 1 : 0;
          // if (arcEnd1.getX() > arcEnd2.getX() && this.collisionAxis.getNormalized().getY() > 0) {
          //   sl = 1 - sl;
          // } else if (arcEnd1.getY() < arcEnd2.getY() && this.collisionAxis.getNormalized().getX() > 0) {
          //   sl = 1 - sl;
          // }
          let lf = testAngle > Math.PI / 2 ? 1 : 0;
          //ctx.addArc(arcEnd2.getX(), arcEnd2.getY(), 40, 40, 0, 0, sf, arcEnd1.getX(), arcEnd1.getY(), this.id + "_arc1");
          ctx.addArc2(arcEnd2, arcMid2, arcMid1, arcEnd1, this.id + "_arc2");
          //ctx.addArc(arcStart.getX(), arcStart.getY(), 40, 40, 0, 0, 1 - sl, arcEnd2.getX(), arcEnd2.getY(), this.id + "_arc2");
          if (arcMid.getLength() > 15) {
            let arcMid1 = arcStart.getAddedVector(this.collisionAxis.getNormalized().getMult(-20)).getAddedVector(arcMid.getMult(-0.15));
            let arcMid2 = arcStart.getAddedVector(this.collisionAxis.getNormalized().getMult(-20)).getAddedVector(arcMid.getMult(0.15));
            ctx.addText(arcMid1.getX() - 1, arcMid1.getY(), "", this.id + "_arc_text1");
            ctx.addText(arcMid2.getX() - 1, arcMid2.getY(), "", this.id + "_arc_text2");
          }
        }

        ctx.addArrow(this.pos.getX(), this.pos.getY(), arrEnd.getX(), arrEnd.getY(), this.id + "_arr");
        ctx.addText(arrEnd.getX() - 5, arrEnd.getAddedVector(this.collisionAxis.getNormalized().getMult(5)).getY(), "n", this.id + "_arr_text");
        ctx.popStyle();
      }

      ctx.pushStyle();
      let rgb = this.reflectingIntensities;
      ctx.setFill(`rgb(${rgb[0]},${rgb[1]},${rgb[2]})`);
      ctx.addCollision(this.pos.getX(), this.pos.getY(), 10, this.id, this);
      ctx.setFill((rgb[0] + rgb[1] + rgb[2]) / 3 > 128 ? "black" : "white");
      ctx.addText(this.pos.getX(), this.pos.getY() + 4, this.collisionObject.getName() + this.collisionObject.getAndIncrementCollisionIndex(), this.id + "_t");
      ctx.popStyle();
    }
    ctx.pushStyle();
    ctx.setStroke(ctx.getDrawState().getShadowRayColor());
    for (let i = 0; Main.Instance.getCTX().getDrawState().getDisplayShadowRays() && i < this.shadowRays.length; i++) {
      this.shadowRays[i].draw(ctx, onlyCollision);
    }
    ctx.popStyle();

    this.nextRay?.draw(ctx, onlyCollision);
  }

  private reset() {
    this.nextRay = null;
    this.shadowRays = [];
  }
  /**
   * @description Recursive method to calculate all rays and ray collisions
   * @param  {(Line|Rect)[]} collisionCollection Array with objects to collide with
   * @param  {Lamp[]} lamps Array with all Lamps for shadow rays
   */
  public recalculateRay(collisionCollection: (Line | Rect)[], lamps: Lamp[]) {
    this.reset();

    //If collision happend, do shadow rays, and further recursive rays

    //Shadow Rays
    for (let i = 0; i < lamps.length; i++) {
      if (lamps[i].getOn() == false) continue;
      this.shadowRays.push(
        new ShadowRay(this.pos, lamps[i].getPosition().getAddedVector(this.pos.getInverted()), lamps[i], this.id + "_sr" + i + "_" + this.depth.toString())
      );
      this.shadowRays[this.shadowRays.length - 1].recalculateRay(collisionCollection, lamps);
    }

    if (this.depth < this.maxDepth) {
      //If no axis could be found, do mirror-like reflecting
      this.nextRay = new Ray(
        this.pos,
        this.collisionLine.getDirection(),
        this.depth + 1,
        this.maxDepth,
        this.prevRay.getId() + "_" + (this.depth + 1).toString()
      );
      this.nextRay.recalculateRay(collisionCollection, lamps);

      //this.recalculateReflectingIntensities(difAngle, specAngle);
    }
    this.recalculateReflectingIntensities();
  }

  /**
   * @description Method to return the colors of ray collisions
   * @param  {number} difAngle Angle of diffuse ray
   * @param  {number} specAngle Angle of specular ray
   */
  public recalculateReflectingIntensities() {
    let r = 0;
    let g = 0;
    let b = 0;
    let diffuse = [0, 0, 0];
    let specular = [0, 0, 0];
    let reflect = [0, 0, 0];
    //If there are further rays, set diffuse RGB to color from diffuse ray
    for (let sr of this.shadowRays) {
      if (sr.getBlocked() == true) {
      } else {
        let li = sr.getLamp().getLampMaterial().getIntensity() / (sr.getLine().getRealLength() / 100 + 1) ** 2;
        let col = sr.getLamp().getLampMaterial().getColor();
        if (col[0] == "#") {
          r = parseInt(col.slice(1, 3), 16);
          g = parseInt(col.slice(3, 5), 16);
          b = parseInt(col.slice(5, 7), 16);
        }
        //Diffuse
        let difAngle = this.collisionAxis.getAngleBetween(sr.getLine().getDirection());
        let difFactor = this.collisionObject.getMaterial().getDiffuse();
        diffuse[0] += (difFactor * Math.cos(difAngle) * li * r) / 255;
        diffuse[1] += (difFactor * Math.cos(difAngle) * li * g) / 255;
        diffuse[2] += (difFactor * Math.cos(difAngle) * li * b) / 255;

        //Specular
        let specAngle = this.collisionLine.getDirection().getAngleBetween(sr.getLine().getDirection());
        let specFactor = this.collisionObject.getMaterial().getSpecular();
        let exponent = 2 ** ((1 - specFactor) * 8);
        let korrFaktor = (exponent + 2) / (2 * Math.PI);
        if (this.collisionLine.getDirection().getDot(sr.getLine().getDirection()) > 0) {
          specular[0] += (korrFaktor * Math.cos(specAngle) ** exponent * li * r) / 10;
          specular[1] += (korrFaktor * Math.cos(specAngle) ** exponent * li * g) / 10;
          specular[2] += (korrFaktor * Math.cos(specAngle) ** exponent * li * b) / 10;
        }
      }
    }
    if (this.nextRay != null) {
      let reflFactor = 1 - this.collisionObject.getMaterial().getDiffuse();
      let col = this.nextRay.getReflectingIntensities();
      reflect[0] += reflFactor * col[0];
      reflect[1] += reflFactor * col[1];
      reflect[2] += reflFactor * col[2];
    }
    if (this.shadowRays.length > 0) {
      diffuse[0] /= this.shadowRays.length;
      diffuse[1] /= this.shadowRays.length;
      diffuse[2] /= this.shadowRays.length;
      specular[0] /= this.shadowRays.length;
      specular[1] /= this.shadowRays.length;
      specular[2] /= this.shadowRays.length;
    }
    //Constant ambient
    let ambient_intensity = 0.1;
    let ambient = [255 * ambient_intensity, 255 * ambient_intensity, 255 * ambient_intensity];
    let li = 0;
    //Sum up Lamps intensities
    // for (let sr of this.shadowRays) {
    //   if (!sr.getBlocked()) {
    //     li += sr.getLamp().getLampMaterial().getIntensity() / (sr.getLine().getRealLength() / 200 + 1) ** 2;
    //   }
    // }
    //Get color from col object
    if (this.collisionObject instanceof Rect) {
      let color = this.collisionObject.getColor();
      if (color[0] == "#") {
        r = parseInt(color.slice(1, 3), 16);
        g = parseInt(color.slice(3, 5), 16);
        b = parseInt(color.slice(5, 7), 16);
      }
      //Add color  * lamp_intensity + ambient
      //console.log(ambient, diffuse, specular);
      r = ambient[0] + (r * diffuse[0]) / 2 + specular[0]; //+ reflect[0];
      g = ambient[1] + (g * diffuse[1]) / 2 + specular[1]; //+ reflect[1];
      b = ambient[2] + (b * diffuse[2]) / 2 + specular[2]; //+ reflect[2];
    } else {
      //If no collision, just use ambient
      r = ambient[0];
      g = ambient[1];
      b = ambient[2];
    }

    //Constrain to int rgb
    r = this.constrain255(Math.round(r));
    g = this.constrain255(Math.round(g));
    b = this.constrain255(Math.round(b));
    this.reflectingIntensities = [r, g, b];
    //console.log(this.reflectingIntensities);
  }

  private constrain255(x: number) {
    return x < 0 ? 0 : x > 255 ? 255 : x;
  }
}
