import { DrawContext } from "src/gui_classes/DrawContext";
import { Vector } from "./vector";

export class Line {
  private pos: Vector;
  private dir: Vector;
  private len: number;

  constructor(position: Vector, direction: Vector, length: number) {
    this.pos = position;
    this.dir = direction;
    this.len = length;
  }

  public getPosition() {
    return this.pos;
  }

  public getEndPosition() {
    return this.pos.getAddedVector(this.dir.getMult(this.len));
  }

  public getDirection() {
    return this.dir;
  }

  public getLength() {
    return this.len;
  }

  public getRealLength() {
    return this.dir.getMult(this.len).getLength();
  }

  public draw(ctx: DrawContext, id: string = "", dashed = false) {
    let posDir = this.getEndPosition();
    ctx.addLine(this.pos.getX(), this.pos.getY(), posDir.getX(), posDir.getY(), id, dashed);
  }

  public getNormal() {
    return this.dir.getNormal();
  }

  /**
   * @description Calculates the collision point between this line and line2
   * @param  {Line} line2 Line to check collision with
   * @returns {[number | undefined, number | undefined, number, number, Line, Line]} Array with collision data
   */
  getIntersect(line2: Line): [number | undefined, number | undefined, number, number, Line, Line] {
    if (this.dir.isParallel(line2.getDirection())) {
      //If parallel, don't return a collision
      return [, , 1, 1, this, line2];
    } else {
      //Else, calculate the collision point and values

      //Calculate direction of collision point from this lines start
      let dir =
        (line2.dir.getX() * (this.pos.getY() - line2.pos.getY()) - line2.dir.getY() * (this.pos.getX() - line2.pos.getX())) /
        (line2.dir.getY() * this.dir.getX() - line2.dir.getX() * this.dir.getY());

      //Calculate the x and y of the collision
      let x = this.pos.getX() + dir * this.dir.getX();
      let y = this.pos.getY() + dir * this.dir.getY();

      //Calculate the scalars for the length at which the position happend for both lines
      let t1 = 0;
      let t2 = 0;
      //switch betwenn x and y to reduce errors
      if (Math.abs(this.dir.getX()) > 0.1) {
        t1 = (x - this.pos.getX()) / this.dir.getX();
      } else {
        t1 = (y - this.pos.getY()) / this.dir.getY();
      }
      if (Math.abs(line2.dir.getX()) > 0.1) {
        t2 = (x - line2.pos.getX()) / line2.dir.getX();
      } else {
        t2 = (y - line2.pos.getY()) / line2.dir.getY();
      }

      //Round values to 4 numbers after the point
      x = Math.round(x * 1000) / 1000;
      y = Math.round(y * 1000) / 1000;
      t1 = Math.round(t1 * 1000) / 1000;
      t2 = Math.round(t2 * 1000) / 1000;

      return [x, y, t1, t2, this, line2];
    }
  }
}
