import { CANVAS, CONTEXT_MENU } from "../app";
import { ContextElement } from "./ContextElement";

// export type ContextMenuAttributes = [string[], string[], string[], number[]];
export type ContextMenuAttributes = { name: string; id: string; type: string; value: string | number };

export class ContextMenu {
  private x: number;
  private y: number;
  private w: number;
  private h: number;

  private elements: ContextElement[];

  private static _instance: ContextMenu;

  private constructor() {
    this.x = 0;
    this.y = 0;
    this.w = 0;
    this.h = 0;
    this.elements = [];
    this.setup();
    this.updateStyle();
  }

  public static get Instance() {
    return this._instance || (this._instance = new this());
  }

  private setup() {
    CONTEXT_MENU.oncontextmenu = function (evt) {
      evt.preventDefault();
      evt.stopPropagation();
    };
  }

  public reset() {
    this.x = 0;
    this.y = 0;
    this.w = 0;
    this.h = 0;
    this.elements = [];
    let lc = CONTEXT_MENU.lastChild;
    while (lc != null) {
      CONTEXT_MENU.removeChild(lc);
      lc = CONTEXT_MENU.lastChild;
    }
    this.updateStyle();
  }

  public updateStyle() {
    CONTEXT_MENU.style.left = (this.x + window.scrollX).toString() + "px";
    CONTEXT_MENU.style.top = (this.y + window.scrollY).toString() + "px";
    CONTEXT_MENU.style.width = this.w.toString() + "px";
    if (this.w == 0) {
      CONTEXT_MENU.style.display = "none";
    } else {
      CONTEXT_MENU.style.display = "inline-block";
    }
    //CONTEXT_MENU.style.height = this.h.toString() + "px";
  }

  public updateElements() {
    for (let e of this.elements) {
      CONTEXT_MENU.appendChild(e.getHTMLElement());
    }
  }

  public draw(x: number, y: number, contents: ContextMenuAttributes[], element: any) {
    this.reset();
    for (let i = 0; i < contents.length; i++) {
      this.elements.push(new ContextElement(x, y + 17 * i, contents[i].name, contents[i].id, contents[i].type, contents[i].value, element));
    }
    this.updateElements();
    this.x = x - 5; //CANVAS.getBoundingClientRect().left + x;
    this.y = y - 5; //CANVAS.getBoundingClientRect().top + y;
    this.w = 175;
    this.h = 23 * this.elements.length;
    this.updateStyle();
  }
}
