import { Line } from "./line";
import { Ray } from "./ray";
import { Rect } from "./rect";
import { Vector } from "./vector";
/**
 * @description Collision Event base class
 * @param  {Line} ray The Ray for the collision calculation
 * @param  {Line|Rect} object The Line or Rect for the collision calculation
 */
export class CollisionEvent {
  protected line: Line;
  protected object: Line | Rect;
  protected lines: Line[];

  constructor(ray: Line, object: Line | Rect) {
    this.line = ray;
    this.object = object;
    this.lines = [];
  }

  getLine() {
    return this.line;
  }

  getObject() {
    return this.object;
  }

  getNewLines() {
    return this.lines;
  }

  calculateCollision() {}
}
/**
 * @description Collision Event for Ray-Line collisions
 * @param  {Line} ray The Ray for the collision calculation
 * @param  {Line} object The Line for the collision calculation
 */
export class CollisionLineEvent extends CollisionEvent {
  protected object: Line;

  constructor(ray: Line, object: Line) {
    super(ray, object);
    this.object = object;
    this.lines = [];
    this.calculateCollision();
  }
  /**
   * @description Calculates the collision point between a ray and a Line
   */
  calculateCollision() {
    this.lines = [];
    let [x, y, t1, t2] = this.object.getIntersect(this.line);
    if (
      x != undefined &&
      y != undefined &&
      t1 != undefined &&
      t1 > 0 &&
      t1 < this.object.getLength() &&
      t2 != undefined &&
      t2 > 0 &&
      t2 < this.line.getLength()
    ) {
      let tempLine = new Line(this.line.getPosition(), this.line.getDirection(), t2);
      if (tempLine.getRealLength() > 2) {
        this.line = tempLine;
        this.lines.push(
          new Line(new Vector(x, y), this.object.getNormal().getRotated(this.object.getNormal().getAngleBetween(this.line.getDirection().getInverted())), 1)
        );
      }
    }
  }
}

/**
 * @description Collision Event for Ray-Rect collisions
 * @param  {Line} ray The Ray for the collision calculation
 * @param  {Line} object The Rect for the collision calculation
 */
export class CollisionRectEvent extends CollisionEvent {
  protected object: Rect;

  constructor(ray: Line, object: Rect) {
    super(ray, object);
    this.object = object;
    this.lines = [];
    this.calculateCollision();
  }

  calculateCollision() {
    this.lines = [];
  }
}
