import { DrawContext } from "../gui_classes/DrawContext";
import { Vector } from "./vector";
import { ContextMenuAttributes } from "../gui_classes/ContextMenu";
import { CANVAS } from "../app";
import { LampMaterial } from "../render_classes/lampMaterial";

/**
 * @description Lamps give light to the colors calculation if hit by shadow ray
 * @param  {Vector} position Starting Position
 * @param  {LampMaterial} lampMaterial Material of the lamp, handles intensity
 * @param  {string} id Identifikationstring for HTML
 */
export class Lamp {
  private pos: Vector;
  private id: string;
  private delete: boolean; //Is true if it should be deleted

  private lampMaterial: LampMaterial;
  private on: boolean; //If true, lamp is on

  constructor(position: Vector, lampMaterial: LampMaterial, id: string) {
    this.pos = position;
    this.lampMaterial = lampMaterial;
    this.on = true;
    this.id = id;
    this.delete = false;
  }

  public getPosition() {
    return this.pos;
  }

  public getLampMaterial() {
    return this.lampMaterial;
  }

  public getOn() {
    return this.on;
  }

  public getId() {
    return this.id;
  }

  public getDelete() {
    return this.delete;
  }

  public move(x: number, y: number, dx: number, dy: number) {
    //this.pos.add(dx, dy);
    this.pos = new Vector(x - CANVAS.getBoundingClientRect().left, y - CANVAS.getBoundingClientRect().top);
  }

  public onOff() {
    this.on = !this.on;
  }

  public doFunction(func: string, val: number, e: any) {
    switch (func) {
      case "move":
        this.move(e.x, e.y, e.movementX, e.movementY);
        break;
      case "onOff":
        this.onOff();
        break;
      case "intensity":
        let numberPick = document.getElementById("numberPicker_intensity") as HTMLInputElement;
        if (numberPick == null) {
        } else {
          numberPick.oninput = () => {
            this.lampMaterial.setIntensity(Number(numberPick.value));
            if (Number(numberPick.value) < 1) {
              numberPick.value = "1";
            } else if (Number(numberPick.value) > 100) {
              numberPick.value = "100";
            }
          };
        }
        break;
      case "delete":
        this.delete = true;
        break;
    }
  }

  public getContextAttributes(): ContextMenuAttributes[] {
    return [
      { name: "On/Off", id: "onOff", type: "lever", value: Number(this.on) },
      { name: "Intensity", id: "intensity", type: "number", value: this.lampMaterial.getIntensity() },
      { name: "Remove", id: "delete", type: "", value: 0 },
    ];
  }

  public draw(ctx: DrawContext) {
    ctx.pushStyle();
    ctx.setFill("#e0e0e0");
    if (this.on) {
      if (ctx.getDrawState().getDarkMode()) {
        ctx.setFill("#000000");
      } else {
        ctx.setFill("#ffffff");
      }
    }
    ctx.addCircle(this.pos.getX(), this.pos.getY(), 7, this.id + "_0" + (this.on ? "" : "_off"), this);
    ctx.popStyle();
    ctx.pushStyle();
    ctx.addLine(this.pos.getX() - 5, this.pos.getY() - 5, this.pos.getX() + 5, this.pos.getY() + 5, this.id + "_1");
    ctx.addLine(this.pos.getX() - 5, this.pos.getY() + 5, this.pos.getX() + 5, this.pos.getY() - 5, this.id + "_2");
    ctx.popStyle();
  }
}
