import { CANVAS, height, width } from "../app";
import { DrawContext } from "src/gui_classes/DrawContext";
import { Line } from "./line";
import { Rect } from "./rect";
import { Vector } from "./vector";
import { Ray } from "./ray";
import { Lamp } from "./lamp";
import { ContextMenuAttributes } from "../gui_classes/ContextMenu";
import { ProjectLine } from "../render_classes/projectLine";
import { GUIDraw } from "../gui_classes/GUIDraw";

/**
 * @description Class which handles all of the camera
 * @param  {Vector} position Position of the camera
 * @param  {Vector} direction Direction of the camera
 * @param  {number} depth Starting depth for the first ray
 * @param  {number} maxDepth Maximum depth for the rays
 * @param  {string} id Identifikationstring for HTML
 */
export class Camera {
  private pos: Vector;
  private dir: Vector;
  private id: string;
  private name: string; //Displayed in SVG

  private amount: number; //Number of ray
  private angle: number; //View angle in which rays are created
  private depth: number; //Starting depth fro rays
  private maxDepth: number; //Maximum ray depth
  private rays: Ray[]; //The Arrays witch all the rays
  private oneRayMode: boolean; //A mode which only displays one ray, if true
  private drawRays: boolean; //A mode which toggles ray visibility, visible if true
  private rayIndex: number; //Current ray index for one ray mode

  private projectLine: ProjectLine; //The screen on which the raytracing is displayed

  constructor(position: Vector, direction: Vector, depth: number, maxDepth: number, id: string, name: string = "") {
    this.pos = position;
    this.dir = direction;
    this.id = id;
    this.name = name;
    this.maxDepth = maxDepth;
    this.depth = depth;
    this.rays = [];
    this.oneRayMode = false;
    this.drawRays = true;
    this.rayIndex = 0;

    this.amount = 1;
    this.angle = Math.PI / 2;
    this.createRays();

    this.projectLine = new ProjectLine(this.pos, this.dir, 50, this.id + "_pl_");
  }

  public getRays() {
    return this.rays;
  }

  public getId() {
    return this.id;
  }

  public getName() {
    return this.name;
  }

  public getAmount() {
    return this.amount;
  }

  public getAngle() {
    return this.angle;
  }

  public getDepth() {
    return this.depth;
  }

  public getMaxDepth() {
    return this.maxDepth;
  }
  /**
   * @desription Enables the mode in which only one ray is drawn at a time
   * @param  {boolean} b New value of the mode, True = only one ray
   */
  public setOneRayMode(b: boolean) {
    this.oneRayMode = b;
  }
  /**
   * @description Sets if rays should be visible
   * @param  {boolean} b New value, True = visible
   */
  public setDrawRays(b: boolean) {
    this.drawRays = b;
    GUIDraw.Instance.needRedraw();
  }

  public getRayIndex() {
    return this.rayIndex;
  }

  public setRayIndex(x: number) {
    this.rayIndex = x;
  }
  /**
   * @description Increments the current ray index for one ray mode
   */
  public incrementRayIndex() {
    this.rayIndex = this.rays.length > 0 ? (this.rayIndex + 1) % this.rays.length : 0;
  }

  public raysDraw(ctx: DrawContext, onlyCollision = false) {
    if (this.oneRayMode && ctx.getDrawState().getAnimatedCamera() == false) {
      return; //Do not draw Animation camera rays
    }

    //Draw Rays
    if (!this.drawRays || !ctx.getDrawState().getDisplayRays() /*&& this.rays.length > 0*/) {
      //this.rays[0].draw(ctx);
      //this.rays[this.rays.length - 1].draw(ctx);
    } else if (this.oneRayMode && this.rays.length > 0 && this.rayIndex >= 0 && this.rayIndex < this.rays.length) {
      this.rays[this.rayIndex].draw(ctx, onlyCollision);
    } else {
      for (let r of this.rays) {
        r.draw(ctx, onlyCollision);
      }
    }
  }

  public draw(ctx: DrawContext) {
    if (this.oneRayMode && ctx.getDrawState().getAnimatedCamera() == false) {
      return; //Do not draw Animation camera
    }

    //Draw Collisions
    this.raysDraw(ctx, true);

    //Draw Camera
    let normalizedDir = this.dir.getNormalized();
    let normal = normalizedDir.getNormal();
    ctx.pushStyle();
    ctx.setFill(ctx.getDrawState().getDarkMode() ? "black" : "white");
    ctx.addCam(this.pos.getX(), this.pos.getY(), normalizedDir, 25, this.id, this.name, this);
    ctx.popStyle();
    //Draw Screen
    this.projectLine.setColors(this.getRayColors());
    this.projectLine.setPosition(this.pos.getAddedVector(normalizedDir.getMult(25).getAddedVector(normal.getMult(25))));
    this.projectLine.setDirection(normalizedDir);
    this.projectLine.draw(ctx);
  }

  //Move for mouse movements
  move(x: number, y: number, dx: number, dy: number) {
    //this.pos.add(dx, dy);
    this.pos = new Vector(x - CANVAS.getBoundingClientRect().left, y - CANVAS.getBoundingClientRect().top);
    this.createRays();
  }

  //Rotate for mouse movements
  rotate(x: number, y: number, dx: number, dy: number) {
    this.dir = new Vector(x - CANVAS.getBoundingClientRect().left - this.pos.getX(), y - CANVAS.getBoundingClientRect().top - this.pos.getY());
    this.createRays();
  }

  /**
   * @description Function that is called from the Context Menu
   * @param  {string} func The id of the Context Element
   * @param  {number} val The value of the Context Element
   * @param  {any} e The mouse event from the Context Element
   */
  doFunction(func: string, val: number, e: any) {
    if (func == "rotate") {
      this.rotate(e.x, e.y, e.movementX, e.movementY);
    } else if (func == "rotation") {
      //Setting rotation from Context Menu Input Element
      let numberPick = document.getElementById("numberPicker_rotation") as HTMLInputElement;
      if (numberPick == null) {
      } else {
        numberPick.oninput = () => {
          let a = (Number(numberPick.value) / 180) * Math.PI;
          this.dir = new Vector(Math.cos(a), Math.sin(a));
          this.createRays();
        };
      }
    } else if (func == "rays") {
      //Setting number of rays from Context Menu Input Element
      let numberPick = document.getElementById("numberPicker_rays") as HTMLInputElement;
      if (numberPick == null) {
      } else {
        numberPick.oninput = () => {
          if (Number(numberPick.value) < 1) {
            numberPick.value = "1";
          }
          this.setRays(Number(numberPick.value));
          this.createRays();
        };
      }
    } else if (func == "rayDepth") {
      //Setting max ray depth from Context Menu Input Element
      let numberPick = document.getElementById("numberPicker_rayDepth") as HTMLInputElement;
      if (numberPick == null) {
      } else {
        numberPick.oninput = () => {
          if (Number(numberPick.value) < 1) {
            numberPick.value = "1";
          }
          this.maxDepth = Number(numberPick.value);
          this.createRays();
          GUIDraw.Instance.needRedraw();
        };
      }
    } else if (func == "angle") {
      //Setting view angle from Context Menu Input Element
      let numberPick = document.getElementById("numberPicker_angle") as HTMLInputElement;
      if (numberPick == null) {
      } else {
        numberPick.oninput = () => {
          if (Number(numberPick.value) < 1) {
            numberPick.value = "1";
          } else if (Number(numberPick.value) > 90) {
            numberPick.value = "90";
          }
          let a = (Number(numberPick.value) / 180) * Math.PI;
          this.setRays(this.amount, a);
          this.createRays();
        };
      }
    } else if (func == "draw_rays") {
      //Toggles ray visibility
      this.setDrawRays(!this.drawRays);
    } else if (func == "new_rays") {
      //Calculates new rays
      this.createRays();
    }
  }
  /**
   * @description Method to return ContextMenuAttributes array to the Context Menu
   * @returns ContextMenuAttributes
   */
  getContextAttributes(): ContextMenuAttributes[] {
    return [
      { name: "Rotation", id: "rotation", type: "number", value: Math.round((Math.atan2(this.dir.getY(), this.dir.getX()) / Math.PI) * 180) },
      { name: "Rays", id: "rays", type: "number", value: this.amount },
      { name: "Ray Depth", id: "rayDepth", type: "number", value: this.maxDepth },
      { name: "Ray Angle", id: "angle", type: "number", value: Math.round((this.angle / Math.PI) * 180) },
      { name: "Draw Rays", id: "draw_rays", type: "lever", value: Number(this.drawRays) },
      // { name: "New Random Rays", id: "new_rays", type: "", value: 0 },
    ];
  }
  //

  /**
   * @description Clears rays
   */
  private reset() {
    this.rays = [];
  }

  /**
   * @description Set number and view angle of rays
   * @param  {number=this.amount} n Number of Rays
   * @param  {number=this.angle} a Viewing angle
   */
  public setRays(n: number = this.amount, a: number = this.angle) {
    this.amount = n < 1 ? 1 : n;
    this.angle = a;
    this.createRays();
  }
  /**
   * @description Resets rays, and generates new ones with the new properties
   */
  private createRays() {
    this.reset();
    let update = this.amount % 2 == 0 ? "0" : "";
    if (this.amount > 1) {
      for (let i = 0; i < this.amount; i++) {
        this.rays[i] = new Ray(
          this.pos,
          this.dir.getRotated(-this.angle / 2 + (this.angle / (this.amount - 1)) * i),
          0,
          this.maxDepth,
          this.id + "_ray" + update + i
        );
      }
    } else {
      this.rays[0] = new Ray(this.pos, this.dir, 0, this.maxDepth, this.id + "_ray_single");
    }
  }

  /**
   * @description Start of recursive Method to calculate all rays and ray collisions
   * @param  {(Line|Rect)[]} collisionCollection Array with objects to collide with
   * @param  {Lamp[]} lamps Array with all Lamps for shadow rays
   */
  public recalculateRay(collisionCollection: (Line | Rect)[], lamps: Lamp[]) {
    if (this.oneRayMode && this.rays.length > 0 && this.rayIndex >= 0 && this.rayIndex < this.rays.length) {
      //In one ray mode recalculate only current ray index ray
      this.rays[this.rayIndex].recalculateRay(collisionCollection, lamps);
    } else {
      //Otherwise recalculate all rays
      for (let r of this.rays) {
        r.recalculateRay(collisionCollection, lamps);
      }
    }
  }
  /**
   * @description Method to return the colors of ray collisions
   * @return {colors} RGB values of current ray collision
   */
  private getRayColors() {
    let colors = [];
    for (let r of this.rays) {
      let col = r.getReflectingIntensities();
      let rh = Math.round(col[0]).toString(16).length == 1 ? "0" + col[0].toString(16) : col[0].toString(16);
      let gh = Math.round(col[1]).toString(16).length == 1 ? "0" + col[1].toString(16) : col[1].toString(16);
      let bh = Math.round(col[2]).toString(16).length == 1 ? "0" + col[2].toString(16) : col[2].toString(16);
      colors.push("#" + rh + gh + bh);
    }
    return colors;
  }
}
