export class Vector {
  private x: number;
  private y: number;
  constructor(x: number, y: number) {
    this.x = x;
    this.y = y;
  }

  public getX() {
    return this.x;
  }

  public getY() {
    return this.y;
  }

  public getLength() {
    return Math.sqrt(this.x ** 2 + this.y ** 2);
  }

  public normalize() {
    if (this.getLength() != 0) this.mult(1 / this.getLength());
  }

  public getNormalized(): Vector {
    let v = new Vector(this.x, this.y);
    v.normalize();
    return v;
  }

  public invert() {
    this.mult(-1);
  }

  public getInverted() {
    return new Vector(-this.x, -this.y);
  }

  public getNormal() {
    return this.getRotated(-Math.PI / 2);
  }

  public isSame(vec2: Vector) {
    return this.x == vec2.x && this.y == vec2.y;
  }

  public isZero() {
    return this.x == 0 && this.y == 0;
  }

  public add(x: number, y: number) {
    this.x += x;
    this.y += y;
  }

  public getAdded(x: number, y: number) {
    return new Vector(this.x + x, this.y + y);
  }

  public addVector(vector: Vector) {
    this.x += vector.x;
    this.y += vector.y;
  }

  public getAddedVector(vector: Vector) {
    return new Vector(this.x + vector.x, this.y + vector.y);
  }

  public mult(factor: number) {
    this.x *= factor;
    this.y *= factor;
  }

  public getMult(factor: number) {
    return new Vector(this.x * factor, this.y * factor);
  }

  public rotate(angle: number) {
    this.x = this.x * Math.cos(angle) - this.y * Math.sin(angle);
    this.y = this.x * Math.sin(angle) + this.y * Math.cos(angle);
  }

  public getRotated(angle: number) {
    return new Vector(this.x * Math.cos(angle) - this.y * Math.sin(angle), this.x * Math.sin(angle) + this.y * Math.cos(angle));
  }

  public getDot(v: Vector) {
    return this.getX() * v.getX() + this.getY() * v.getY();
  }

  public getDotAngleBetween(v: Vector) {
    return Math.acos(this.getDot(v) / (this.getLength() * v.getLength()));
  }

  public getAngleBetween(v: Vector): number {
    //return Math.atan2(this.y - v.y, this.x - v.x);
    return Math.atan2(this.y, this.x) - Math.atan2(v.y, v.x);
  }

  public isParallel(vec2: Vector) {
    if (this.isZero() || vec2.isZero()) {
      return false;
    }
    // x1 = a * x2
    // y1 = a * y2
    // x1 = y1/y2 * x2
    let v1 = this.getNormalized();
    let v2 = vec2.getNormalized();
    let v3 = v2.getInverted();
    return v1.isSame(v2) || v1.isSame(v3);
  }
}
