import { CONTEXT_MENU } from "../app";
import { Rect } from "../collision_classes/rect";

export class ContextElement {
  private x: number;
  private y: number;
  private w: number;
  private h: number;
  private content: string;
  private func: string;
  private type: string;
  private value: string | number;
  private elem: any;
  private textSize: number;

  constructor(x: number, y: number, content: string, func: string, type: string, value: string | number, element: any, textSize: number = 16) {
    this.x = x;
    this.y = y;
    this.content = content;
    this.func = func;
    this.type = type;
    this.value = value;
    this.elem = element;
    this.textSize = textSize;
    let p = document.createElement("p");
    p.textContent = content;
    p.className = "contextElement";
    p.style.fontSize = textSize + "px";
    let b = p.getBoundingClientRect();
    this.w = b.width;
    this.h = b.height;
    //console.log(this.h);
  }

  public getX() {
    return this.x;
  }

  public getY() {
    return this.y;
  }

  public getContent() {
    return this.content;
  }

  public getWidth() {
    return this.w;
  }

  public getHeight() {
    return this.h;
  }

  public getHTMLElement() {
    let p = document.createElement("div");
    p.textContent = this.content;
    switch (this.type) {
      case "number":
        p.className = "contextElement";
        p.style.fontSize = this.textSize + "px";

        let numPick = document.createElement("input");
        numPick.type = "number";
        numPick.id = "numberPicker_" + this.func;
        numPick.className = "contextElement_input";
        numPick.value = this.value.toString();
        //CONTEXT_MENU.lastChild?.appendChild(colPick);
        p.appendChild(numPick);
        p.onclick = (e: any) => {
          this.elem.doFunction(this.func, this.value, e);
        };
        break;
      case "color":
        p.className = "contextElement";
        p.style.fontSize = this.textSize + "px";

        let colPick = document.createElement("input");
        colPick.type = "color";
        colPick.id = "colorPicker_" + this.func;
        colPick.className = "contextElement_input";
        // colPick.value = (this.elem as Rect).getColor();
        colPick.value = this.value.toString();
        //CONTEXT_MENU.lastChild?.appendChild(colPick);
        p.appendChild(colPick);
        p.onclick = (e: any) => {
          this.elem.doFunction(this.func, this.value, e);
        };
        break;
      case "lever":
        p.className = "contextElement";
        p.style.fontSize = this.textSize + "px";

        let lever = document.createElement("div");

        //let lever = document.createElement("button");
        //lever.className = "mdc-switch";

        //lever.setAttribute("width", "100px");
        lever.innerHTML = ""; //
        if (this.value == 0) {
          lever.className = "contextElement_lever_off";
        } else {
          lever.className = "contextElement_lever_on";
        }
        lever.style.fontSize = this.textSize * 0.75 + "px";
        p.appendChild(lever);

        p.onclick = (e: any) => {
          if (lever.className == "contextElement_lever_on") {
            lever.className = "contextElement_lever_off";
            this.value = 0;
          } else {
            lever.className = "contextElement_lever_on";
            this.value = 1;
          }
          this.elem.doFunction(this.func, this.value, e);
        };
        break;
      default:
        p.className = "contextElement";
        p.style.fontSize = this.textSize + "px";
        p.onclick = (e: any) => {
          this.elem.doFunction(this.func, this.value, e);
        };
    }
    return p;
  }
}
